header {
	@include pos(fixed, 0 0 null 0);
	background: none;
	width: 100%;
	z-index: 999;
	height: 100px;
	background: $white;
	box-shadow: 0 2px 40px rgba(0,0,0,0.15);
	@include media-breakpoint-down(md) {
		@include pos(fixed, 0 0 null 0);
		background: $white;
		box-shadow: 0 2px 2px rgba(0,0,0,0.15);
		text-align: center;
		height: 55px; }
	@include media-breakpoint-down(xs) {
		text-align: left; }

	.sitelogo {
		float: left;
		width: 350px;
		margin-top: 17px;
		@include media-breakpoint-down(md) {
			margin-top: 3px;
			float: none;
			display: inline-block;
			width: unset; }
		@include media-breakpoint-down(md) {
			margin-top: 8px; }
		a {
			display: block;
			img {
				@include media-breakpoint-down(md) {
					height: 40px; } } }


		p {
			@include fs(12rem);
			color: darken($white, 40%);
			text-transform: uppercase;
			margin-top: 8px;
			@include media-breakpoint-down(md) {
				display: none; } } }
	.header-right {
		float: right;
		width: calc(100% - 350px); }
	.tool {
		margin-top: 15px;
		float: right;
		@include media-breakpoint-down(md) {
			width: 100%;
			border-top: 1px dashed darken($white, 15%);
			padding-top: 15px;
			margin-top: 0;
			display: none;
			text-align: center; }
		.hotline {
			float: left;
			margin-right: 17px;
			@include media-breakpoint-down(md) {
				margin-right: 0;
				float: none;
				margin-bottom: 10px; }
			&:after {
				content: '';
				display: inline-block;
				height: 12px;
				width: 1px;
				background: darken($white, 40%);
				margin-left: 17px;
				@include transform(translateY(1px));
				@include media-breakpoint-down(md) {
					display: none; } }
			a {
				color: darken($white, 60%);
				font-weight: 700;
				&:hover {
					color: $green; }
				.fa {
					color: darken($white, 40%);
					margin-right: 5px; } } }

		.mail {
			float: left;
			@include media-breakpoint-down(md) {
				float: none;
				margin-bottom: 10px; }
			// &:after
			/// content: ''
			/// display: inline-block
			/// height: 12px
			/// width: 1px
			/// background: darken($white, 40%)
			/// margin-left: 17px
			/// +transform(translateY(1px))
			/// +media-breakpoint-down(md)
			/// 	display: none
			a {
				color: darken($white, 40%);
				.fa {
					@include fs(16rem); } } }
		.language {
			float: left;
			@include media-breakpoint-down(md) {
				float: none; }
			a {
				display: inline-block;
				+ a {
					margin-left: 10px; } } } }

	.header-bottom {
		float: right;
		clear: both;
		@include media-breakpoint-down(md) {
			@include pos(fix, 0 null 0 0);
			width: 350px;
			z-index: 1000;
			background: $white;
			@include transform(translateX(-100%));
			@include transition(all 0.5s);
			&.open {
				@include transform(translateX(0)); } }
		@include media-breakpoint-down(xs) {
			width: 100%; }
		.sitelogo {
			text-align: center;
			width: 100%;
			margin: 15px 0;
			padding-bottom: 15px;
			border-bottom: 1px solid $green;
			@include media-breakpoint-up(lg) {
				display: none; }
			a {
				img {} } }
		nav.main-menu {
			float: left;
			margin-top: 20px;
			@include media-breakpoint-down(md) {
				width: 100%;
				text-align: center;
				margin-top: 10px; }
			ul {

				li {
					display: inline-block;
					@include media-breakpoint-down(md) {
						display: block;
						margin: 0;
						margin-bottom: 20px; }
					+ li {
						margin-left: 60px;
						@include media-breakpoint-down(lg) {
							margin-left: 25px; }
						@include media-breakpoint-down(md) {
							margin-left: 0; } }
					a {
						display: block;
						@include fs(15rem);
						text-transform: uppercase;
						color: darken($white, 60%);
						@include media-breakpoint-down(md);
						font-weight: 600;



						&:hover {
							color: $green; } }
					&.active {
						a {
							color: $green;
							font-weight: 700; } } } } }
		.tool {
			display: block !important;
			@include media-breakpoint-up(lg) {
				display: none !important; } }
		.search {
			@include media-breakpoint-up(lg) {
				display: block !important; }
			@include media-breakpoint-down(md) {
				display: none; } } }


	.btn-showmenu {
		display: none;
		float: left;
		margin: 18px 20px 15px 0;
		width: 22px;
		height: 18px;
		@include pos(rel,null);
		z-index: 80;
		cursor: pointer;
		@include media-breakpoint-down(xs) {
			margin-right: 10px; }

		span {
			display: block;
			height: 2px;
			width: 80%;
			background: $green;
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include transition(all,0.3s); }
		&:before {
			content: "";
			display: block;
			height: 2px;
			background: $green;
			@include pos(abs, 0 0 null 0);
			@include transition(all,0.3s); }
		&:after {
			content: "";
			display: block;
			height: 2px;
			background: $green;
			@include pos(abs, null 0 0 0);
			@include transition(all,0.3s); }
		&.active {
			span {
				opacity: 0; }
			&:before {
				@include transform(translateY(-50%)rotateZ(45deg));
				top: 50%; }
			&:after {
				@include transform(translateY(50%)rotateZ(-45deg));
				bottom: 50%; } }
		@include media-breakpoint-down(md) {
			display: block; } }
	.btn-closemenu {
		@include pos(abs, 5px 5px null null);
		background: color-rgba($green, 0.85);
		width: 30px;
		text-align: center;
		height: 30px;
		line-height: 30px;
		@include bor-rad(50%);
		display: none;
		float: left;
		z-index: 80;
		cursor: pointer;
		color: $white;
		@include media-breakpoint-down(md) {
			display: block; } }
	.search {
		position: relative;
		float: left;
		margin-left: 35px;
		padding: 17px 0;
		@include media-breakpoint-up(lg) {
			display: none; }
		@include media-breakpoint-down(lg) {
			margin-left: 25px; }
		@include media-breakpoint-down(md) {
			float: right;
			padding: 12px 0; }


		@include media-breakpoint-down(xs) {
			margin-left: 10px; }
		.searchtoggle {
			@include fs(16rem);
			color: $green;
			font-weight: 700;
			cursor: pointer;
			@include transition(all 0.3s);
			@include media-breakpoint-down(md) {
				@include fs(20rem);
				color: $green; }

			&:hover {
				color: $hover; } }
		.searchwrap {
			@include pos(abs, calc(100% + 5px) 0 null null);
			width: 250px;
			background: $green;
			height:  40px;
			line-height: 40px;
			padding: 0 15px;
			@include bor-rad(5px);
			z-index: 100;
			display: none;
			@include media-breakpoint-down(md) {
				top: calc(100% + 1px); }

			&.open {
				@include transform(translateX(0));
				opacity: 1;
				visibility: visible; }
			input {
				float: left;
				width: calc(100% - 30px);
				background: none;
				border: none;
				line-height: 40px;

				color: $white;
				&::-webkit-input-placeholder {
					@include fs(13rem); }

				&::-moz-placeholder {
					font-style: italic; }
				&:-ms-input-placeholder {
					font-style: italic; }
				&:-moz-placeholder {
					font-style: italic; } }

			.searchbutton {
				float: left;
				display: inline-block;
				width: 30px;
				color: rgba(255,255,255,0.8);
				font-weight: 300;
				@include fs(18rem);
				line-height: 38px; } } } }


.btn-scrolltop {
	@include pos(fix, null 20px 20px null);
	width: 40px;
	height: 40px;
	color: $white;
	background: $green;
	box-shadow: 0 0px 2px rgba(0,0,0,0.2);
	line-height: 35px;
	text-align: center;
	@include fs(28rem);
	cursor: pointer;
	@include transition(all 0.3s);
	opacity: 0;
	z-index: 10;
	&:hover {
		background: lighten($green, 10%); }
	@include media-breakpoint-down(xs) {
		width: 30px;
		height: 30px;
		line-height: 20px;
		bottom: 5px;
		right: 5px; }

	&.appear {
		opacity: 1; } }
.overlay {
	@include pos(fixed, 0 0 0 0);
	background: rgba(0,0,0,0.7);
	z-index: 998;
	display: none; }
