.pages {
	text-align: center;
	margin: 0;
	margin-bottom: 20px;
	.pagination {
		display: inline-block;
		margin: 0;
		li {
			display: inline-block;
			margin: 0 2px;
			a,span {
				@include fs(14rem);
				display: inline-block;
				width: 25px;
				height: 25px;
				line-height: 25px;
				text-align: center;
				color: darken($white, 40%);
				@include bor-rad(2px); }
			&.active {
				a,span {
					color: $white !important;
					background: $green; } }

			&:hover {
				a,span {
					@include transform(translateY(-3px));
					box-shadow: 0px 4px 20px rgba(0,0,0,0.25);
					color: $green; } } } } }
