$bluemain: #00477b;

$white: #fff;
$black: #000;
$lblack: #222;
$main: #333;
$blue: #00b2e7;
$dblue: #006aa4;
$yellow: #ffd359;
$orange: #f73936;
$milkorange: #fa706f;
$facebook: #3b5998;
$twitter: #46d4fe;
$rss: #ffa133;

// MATERIAL COLOR
$red: #F44336;
$pink: #E91E63;
$purple: #9C27B0;
$deeppurple: #673AB7;
$indigo: #3F51B5;
$blue: #2196F3;
$lightblue: #03A9F4;
$cyan: #00BCD4;
$teal: #009688;
$green: #4CAF50;
$lightgreen: #8BC34A;
$lime: #CDDC39;
$yellow: #FFEB3B;
$amber: #FFC107;

$deeporange: #FF5722;
$brown: #795548;
$grey: #212121;
$bluegrey: #263238;

// social
$facebook: #3a579b;
$twitter: #00aced;
$google: #dd4b39;
$linkedin: #007bb6;
$instagram: #7d4d38;


// MERAP
$pink: #eb0873;
$green: #01a54f;
$hover: lighten($green, 13%);
$orange: #faa001;

@mixin fs($size) {
	font-size: ($size / 16); }
@mixin block($w,$h,$f) {
	display: block;
	width: $w;
	height: $h;
	float: $f; }
@mixin flex {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flexbox;
	display: flex; }
@mixin mainbutton {
	display: inline-block;
	@include bor-rad(20px);
	height: 35px;
	line-height: 35px;
	padding: 0 30px;
	background: $green;
	color: $white;
	position: relative;
	overflow: hidden;
	@include transition(all 0.3s);
	&:hover {
		background: lighten($green, 10%); } }
body {
	margin: 0;
	padding: 0;
	@include fs(15rem);
	font-family: 'Muli';
	color: $main;
	line-height: 1.5;
	margin: auto;
	@include media-breakpoint-down(sm) {
		@include fs(14rem); }
	// max-width: 1600px
	padding-top: 100px;
	@include media-breakpoint-down(md) {
		padding-top: 55px; } }

.container {
	@include media-breakpoint-up(xl) {
		width: 1170px; } }
html {
	overflow: auto; }
.fullpage {
	@include block(100%,null,left);
	overflow: hidden;
	box-shadow: 0 0 20px color-rgba($black,0.3); }

main {
 }	// background: $white
a {
	@include transition(all,0.3s); }
a, a:hover {
	&:focus, &:active, &:hover {
		text-decoration: none; } }
input,button, select, textarea {
	&:hover, &:active, &:focus {
		outline: none; } }
button {
	border: none;
	background: none;
	margin: 0;
	padding: 0;
	@include transition(all,0.3s); }
img {
	vertical-align: middle; }
main {
	clear: both; }

strong {
	span {
		font-weight: bold; } }

.flex, .flex-wrap {
	clear: both; }

// .container
// 	+media-breakpoint-up(xl)
// 		max-width: 1200px
// 		width: 100%

.content {
	line-height: 1.5;
	@include fs(15rem);
	color: darken($white, 60%);
	@include media-breakpoint-down(sm) {
		@include fs(14rem); }
	ul, ol, p, h1, h2, h3, h4, h5, h6, img, figure {
		margin-bottom: 10px;
		line-height: 1.5; }
	.posttitle {
		// font-weight: 500
		color: $lblack;
		margin-bottom: 5px; }
	h6 {
		color: $lblack;
 }		// font-weight: 500
	h5 {
		@include fs(16rem);
		// font-weight: 500
		color: $lblack; }
	h4 {
		@include fs(18rem);
		color: $lblack;
 }		// font-weight: 500
	// figure
	// 	margin: 0 auto 5px
	// 	img
	// 		margin-bottom: 5px
	// 		width: 100%
	// 	figcaption
	// 		background: darken($white,5%)
	// 		padding: 5px 0
	// 		text-align: center
	// 		color: darken($white,60%)
	ul {
		padding-left: 25px;
		@include media-breakpoint-down(xs) {
			padding-left: 15px; }
		li {
			@include pos(rel,null);
			margin-bottom: 5px;
			&:before {
				content: '';
				display: block;
				width: 5px;
				height: 5px;
				@include bor-rad(50px);
				background: #80d2a7;
				@include pos(abs, 7px null null -15px);
				@include media-breakpoint-down(xs); } } }


	.red {
		color: $red; }
	.simi {
		font-weight: 500; }
	.lblack {
		color: $lblack; } }

.fancybox-container {
	.fancybox-caption-wrap {
		padding-top: 0;
		.fancybox-caption {
			text-align: center;
			border-top: none;
			padding-top: 0;
			margin-bottom: 25px;
			@include fs(16rem);
			font-family: 'Muli';
			display: none; } } }
