
.connguoimerap-page {
	padding: 40px 0;
	position: relative;
	.content {
		margin-bottom: 35px;
		.row {
			margin: 0 -5px;
			.col-xs-12 {
				padding: 5px; } }
		.video {
			position: relative;
			display: block;
			width: 100%;
			overflow: hidden;
			img {
				width: 100%;
				@include transition(all 5s); }
			&:hover {
				img {
					@include transform(scale(1.3,1.3)); } }
			&:before {
				content: '';
				@include pos(abs, 0 0 0 0);
				background: url(../img/icon-video.png) no-repeat;
				background-size: cover;
				width: 40px;
				height: 40px;
				margin: auto;
				z-index: 10; }
			&:after {
				content: '';
				@include pos(abs, 0 0 0 0);
				background: rgba(0,0,0,0.2); } }
		img {
			margin-bottom: 0; }
		.img {
			position: relative;
			display: block;
			width: 100%;
			overflow: hidden;
			img {
				width: 100%;
				@include transition(all 0.3s); }
			&:hover {
				img {
					@include transform(scale(1.1,1.1)); } }

			&:before {
				content: '';
				@include pos(abs, 0 0 0 0);
				background: url(../img/icon-img.png) no-repeat;
				background-size: cover;
				width: 40px;
				height: 40px;
				margin: auto;
				z-index: 10; }
			&:after {
				content: '';
				@include pos(abs, 0 0 0 0);
				background: rgba(0,0,0,0.2); } } } }


.testimonial-list {
	max-width: 900px;
	margin: auto;
	.testimonial-item {
		margin-bottom: 40px;
		padding-bottom: 30px;
		border-bottom: 1px dashed darken($white, 40%);
		position: relative;
		z-index: 100;
		&:last-child {
			border: none;
			margin-bottom: 0;
			padding: 0; }
		figure {
			@include flexbox;
			flex-wrap: wrap;
			.item-img {
				display: flex;
				align-items: center;
				justify-content: center;
				@include bor-rad(50%);
				overflow: hidden;
				width: 150px;
				height: 150px;

				@include media-breakpoint-only(md) {
					width: 130px;
					height: 130px; }
				@include media-breakpoint-down(sm) {
					margin: auto;
					margin-bottom: 20px;
					float: none; }
				img {
					width: 100%; } }
			figcaption {
				padding-left: 30px;

				width: 100%;
				@include media-breakpoint-down(lg) {
					width: 100%;
					float: none;
					display: block;
					padding-left: 0; }
				.item-content {
					margin-bottom: 20px;
					@include media-breakpoint-down(xs) {
						text-align: center; } }
				time {
					float: left;
					letter-spacing: 2px;
					color: darken($white, 70%);
					@include media-breakpoint-down(xs) {
						display: block;
						float: none;
						margin-bottom: 10px;
						text-align: center; } }
				.item-name {
					float: right;
					color: darken($white, 60%);
					@include fs(20rem);
					font-weight: 300;
					text-align: right;
					@include media-breakpoint-down(xs) {
						float: none;
						display: block;
						text-align: left;
						text-align: center; }
					&:before {
						content: '--';
						display: inline-block;
						margin-right: 5px; }
					&:after {
						content: '--';
						display: inline-block;
						margin-right: 5px; } } } } } }
