time {
	display: block;
	@include fs(13rem);
	color: darken($white, 40%);
	margin-bottom: 10px;

	&:before {
		content: '\f017';
		font-family: 'FontAwesome';
		// +fs(16rem)
		display: inline-block;
		margin-right: 5px; } }
.pagetitle {
	color: $main;
	@include fs(36rem);
	text-transform: uppercase;
	text-align: center;
	font-weight: 300;
	margin-bottom: 25px;
	@include media-breakpoint-down(md) {
		@include fs(28rem); }
	@include media-breakpoint-down(sm) {
		@include fs(24rem); }
	&:after {
		content: '';
		display: block;
		margin: auto;
		height: 10px;
		width: 130px;
		background: url(../img/title.png) top center no-repeat;
		margin-top: 5px; } }
.headtitle {
	@include fs(24rem);
	color: $green;
	margin-bottom: 10px;
	font-weight: 500;
	text-transform: uppercase;
	@include media-breakpoint-down(sm) {
		@include fs(20rem); } }

.eventpage-wrap {
	.breadcrumb {
		.breadcrumb-bg {
			height: 100vh; } }
	.pages {
		margin-bottom: 30px; } }

.small-gutter {
	margin: 0 -10px;
	.col-xs-12 {
		padding: 0 10px; } }
.event-hot {
	background: darken($white, 12%);
	padding: 40px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.pagetitle {

		&:after {} } }
.event-hot-slide {
	margin: 0 -15px;
	.item {
		padding: 0 15px;
		.eventcol {
			margin-bottom: 0;
			figure {
				position: relative;
				.eventimg {
					img {
						// -webkit-transform: scale(1, 1) !important
						// -khtml-transform: scale(1, 1) !important
 } }						// transform: scale(1, 1) !important
				figcaption {
					@include pos(abs, null 0 0 0);
					z-index: 10;
					border: none;
					background: -webkit-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3) , rgba(0,0,0,0.4) );
					background: -o-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3) , rgba(0,0,0,0.4) );
					background: -moz-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3) , rgba(0,0,0,0.4) );
					background: linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3) , rgba(0,0,0,0.4) );

					@include transition(all 0.3s);
					padding: 30px 20px 15px; }

				&:hover {
					figcaption {
						@include transform(translateY(0)); } }
				.eventname {
					border: none;
					margin: 0;
					padding: 0;
					letter-spacing: 1px;
					text-align: center;
					@include fs(16rem);
					text-transform: uppercase;
					a {
						color: $white;
						&:hover {
							color: $hover; } } } } } }
	.slick-next, .slick-prev {
		@include fs(0);
		@include pos(abs, 50% null null -15px);
		@include transform(translateY(-50%));
		z-index: 10;
		@include media-breakpoint-down(xs) {
			left: 0; }
		&:before {
			content: '\e875';
			font-family: 'Linearicons-free';
			@include fs(20rem);
			color: darken($white, 60%);
			font-weight: 600; }
		&:hover {
			&:before {
				color: $green; } } }
	.slick-next {
		left: unset;
		right: -15px;
		@include media-breakpoint-down(xs) {
			right: 0; }
		&:before {
			content: '\e876'; } } }

.eventlist {
	.event-item {
		figure {
			margin-bottom: 30px;
			.event-img {
				display: block;
				position: relative;
				overflow: hidden;
				img {
					@include transition(all 0.5s); }

				&:before {
					content: '';
					background: $white;
					@include pos(absolute, -120px null null -170px );
					opacity: 0.3;
					width: 100px;
					height: 400px;
					@include rotate(35deg);
					z-index: 10; }
				&:after {
					content: '';
					background: $white;
					@include pos(absolute, -120px null null -200px );
					opacity: 0.5;
					width: 100px;
					height: 400px;
					@include rotate(35deg);
					z-index: 10; }
				&:hover {
					img {
						@include transform(scale(1.15,1.15));
						transition-delay: 0.3s; }
					// &:before
					// 	left: 150%
					// 	+transition(all .3s)
					&:after {
						left: 150%;
						@include transition(all .7s); } } }

			figcaption {
				padding: 20px 20px 0;
				border: 1px solid darken($white, 8%);
				position: relative;
				background: darken($white, 3%);

				.event-name {
					display: block;
					margin-bottom: 15px;

					a {
						color: $main;
						letter-spacing: 0.5px;
						@include fs(18rem);
						display: block;
						text-transform: uppercase;
						&:hover {
							color: green; } } }


				.event-des {
					color: darken($white, 50%);
					@include fs(14rem);
					display: -webkit-box;
					-webkit-line-clamp: 5;
					-webkit-box-orient: vertical;
					overflow: hidden;
					min-height: 105px;
					margin-bottom: 20px; }
				.event-wrap {
					@include flexbox;
					flex-wrap: wrap;
					padding: 10px 0;
					border-top: 1px solid darken($white, 8%);

					time {
						width: 50%;
						margin-bottom: 0; }
					.event-button {
						width: 50%;
						a {
							float: right;
							color: darken($white, 50%);
							text-transform: uppercase;
							vertical-align: middle;
							display: inline-block;
							@include fs(13rem);
							&:hover {
								color: green; }
							&:after {
								content: '\f105';
								font-family: 'FontAwesome';
								margin-left: 5px; } } } } } } } }

.sidebar-wrap {
	padding-left: 50px;
	@include media-breakpoint-down(lg) {
		padding: 0; }
	.title {
		@include fs(16rem);
		font-weight: 700;
		text-transform: uppercase;
		border-bottom: 1px solid $main;
		margin-bottom: 20px; }
	.hot-event {
		.event-item {
			&:last-child {
				figure {
					border: none; } }
			figure {
				@include flexbox;
				flex-wrap: wrap;
				margin-bottom: 15px;
				padding-bottom: 15px;
				border-bottom: 1px dashed darken($white, 12%);
				.item-img {
					width: 100px;
					@include flexbox;
					align-items: center;
					justify-content: center;
					overflow: hidden;
					img {} }

				figcaption {
					width: calc(100% - 100px);
					padding-left: 15px;

					time {
						display: block;
						@include fs(13rem);
						color: darken($white, 40%);

						&:before {
							content: '\f017';
							font-family: 'FontAwesome';
							// +fs(16rem)
							display: inline-block;
							margin-right: 5px; } }
					.item-title {
						a {
							color: $main;
							font-weight: 500;
							line-height: 1.2;
							display: block;
							margin-bottom: 5px;
							&:hover {
								color: $green; } } } } } } }
	.tags-list {
		a {
			display: inline-block;
			padding: 5px 15px;
			color: darken($white, 50%);
			border: 1px solid darken($white, 30%);
			text-transform: uppercase;
			@include fs(12rem);
			margin: 0 2px 2px 0;
			&:hover {
				background: $green;
				border-color: $green;
				color: $white; } } } }



.news-detail-page {
	.headtitle {
		margin-bottom: 10px; }
	.album-nav {
		display: none; } }
.newsdetail {
	@include media-breakpoint-down(md) {
		margin-bottom: 20px; }
	time {
		margin-bottom: 15px;
		position: relative;
		border-bottom: 1px dashed darken($white, 12%);
		padding-bottom: 15px;
		display: block;
		@include fs(13rem);
		color: darken($white, 40%);

		&:before {
			content: '\f017';
			font-family: 'FontAwesome';
			// +fs(16rem)
			display: inline-block;
			margin-right: 5px; } }

	.content {
		border-bottom: 1px dashed darken($white, 12%);
		h3 {
			text-transform: uppercase; } }
	.news-detail-slide {
		overflow: hidden;
		margin-bottom: 20px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 10px; }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null 0);
			@include fs(0);
			z-index: 10;
			&:before {
				content: '\e875';
				font-family: 'Linearicons-free';
				@include fs(20rem);
				height: 40px;
				line-height: 40px;
				text-align: center;
				padding: 0 5px;
				background: rgba(255,255,255,0.8);
				color: $main;
				display: inline-block;
				@include media-breakpoint-down(sm) {
					height: 35px;
					line-height: 35px;
					padding: 0 2px;
					@include fs(16rem); } }
			&:hover {
				&:before {
					color: $white;
					background: $green; } } }
		.slick-prev {
			@include transform(translate(-100% , -50%)); }
		.slick-next {
			@include transform(translate(100% , -50%));
			left: unset;
			right: 0;
			&:before {
				content: '\e876'; } }
		&:hover {
			.slick-prev, .slick-next {
				@include transform(translate(0% , -50%)); } }
		@include media-breakpoint-down(md) {
			.slick-prev, .slick-next {
				@include transform(translate(0% , -50%)); } } }
	.news-detail-nav {
		margin: 0 -4px;
		margin-bottom: 20px;

		.item {
			padding: 0 4px;
			cursor: pointer;
			&.slick-current {
				img {
					border: 2px solid $green; } } } }



	.likeshare {
		margin-top: 20px;

		a {
			display: inline-block;
			height: 30px;
			line-height: 30px;
			padding: 0 15px;
			color: $white;
			width: 50px;
			text-align: center;
			margin-left: 2px;
			&.facebook {
				background: #3e578c;
				&:hover {
					background: lighten(#3e578c, 10%); } }
			&.google {
				background: #dd4b38;
				&:hover {
					background: lighten(#dd4b38, 10%); } }
			&.twitter {
				background: #00acee;
				&:hover {
					background: lighten(#00acee, 10%); } } } } }
