footer {
	background: $green;
	margin-top: 30px;
	.footer-link {

		ul {
			text-align: center;
			padding: 35px 0 30px;
			border-bottom: 1px solid rgba(255,255,255,0.5);
			@include media-breakpoint-down(xs) {
				padding: 20px 0 15px; }

			li {
				display: inline-block;
				@include media-breakpoint-down(xs) {
					margin-bottom: 5px; }
				a {
					display: block;
					color: $white;
					@include fs(15rem);
					text-transform: uppercase;
					margin: 0 25px;
					@include media-breakpoint-down(sm) {
						margin: 0 10px; }
					&:hover {
						color: $hover; } } } } }
	.social {
		text-align: center;
		margin-bottom: 25px;
		@include media-breakpoint-down(xs) {
			margin-bottom: 30px; }
		a {
			display: inline-block;
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			@include bor-rad(50%);
			border: 1px solid $white;
			text-align: center;
			@include fs(18rem);
			font-weight: 700;
			color: $white;
			margin: 0 5px;
			&:hover {
				background: $white;
				color: $green; } } }
	.footer-content {
		text-align: center;
		color: #b0e8ca;
		padding: 20px 0 25px;
		@include fs(13rem);
		strong {
			@include fs(16rem);
			text-transform: uppercase;
			color: $white;
			font-weight: 600;
			@include media-breakpoint-down(md) {
				margin-bottom: 5px;
				display: block; } }
		a {
			color: #b0e8ca;
			&:hover {
				color: $white; } }
		p, div {
			@include media-breakpoint-down(xs) {
				margin-bottom: 5px; } } } }






