.about-page {
	.breadcrumb-wrap, footer {
		margin: 0; } }
.about-wrap {
	padding: 0 0 50px;
	@include media-breakpoint-down(sm) {
		padding-bottom: 30px; } }
.about-video {
	margin-bottom: 60px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 30px; }
	figure {
		.item-img {
			position: relative;
			overflow: hidden;
			display: block;
			&:before {
				content: '';
				@include pos(abs, 0 0 0 0);
				width: 110px;
				height: 110px;
				background: url('../img/home/icon-video.png') center center no-repeat;
				margin: auto;
				background-size: cover;
				z-index: 10;
				@include media-breakpoint-down(sm) {
					height: 60px;
					width: 60px; } }
			&:after {
				content: '';
				@include pos(abs, 0 0 0 0);
				background: rgba(0,0,0,0.3); }


			img {
				@include transition(all 0.5s);
				width: 100%; }
			&:hover {
				img {
					@include transition(all 5s);
					@include transform(scale(1.3,1.3)); } } } } }

.about-vision {
	figure {
		@include flexbox;
		flex-wrap: wrap;
		background: $white;
		@include media-breakpoint-down(sm) {
			margin-bottom: 20px; }
		&:nth-child(1) {
			@include media-breakpoint-down(sm) {
				background: darken($white, 5%) !important; } }
		figcaption {
			width: 50%;
			padding: 30px;
			min-height: 280px;
			@include flexbox;
			align-items: center;
			align-content: center;
			flex-wrap: wrap;
			@include media-breakpoint-down(sm) {
				width: 100%; }
			@include media-breakpoint-down(xs) {
				order: 1; }
			h3 {
				@include fs(18rem);
				font-weight: 700;
				text-transform: uppercase;
				color: $green; }
			ul {
				li {} } }


		.item-img {
			width: 50%;
			min-height: 250px;
			@include media-breakpoint-down(sm) {
				width: 100%; } } } }
.product-img {
	margin-bottom: 20px; }

.aboutvideo-list {
	.video-item {
		margin-bottom: 30px;
		figure {
			.item-img {
				display: block;
				overflow: hidden;
				margin-bottom: 15px;
				position: relative;
				&:before {
					content: '';
					@include pos(abs, 0 0 0 0);
					width: 50px;
					height: 50px;
					background: url('../img/home/icon-video.png') center center no-repeat;
					margin: auto;
					background-size: cover;
					z-index: 10; }
				&:after {
					content: '';
					@include pos(abs, 0 0 0 0);
					background: rgba(0,0,0,0.3); }
				img {
					@include transition(all 0.3s);
					width: 100%; }
				&:hover {
					img {
						@include transform(scale(1.1,1.1)); } } }
			figcaption {
				color: darken($white, 60%);
				p {
					margin-bottom: 10px; }
				h3 {
					font-weight: 700;
					margin-bottom: 3px; } } } } }
