.bannerwrap {
	margin-bottom: 40px;
	@include media-breakpoint-down(lg) {
		margin-bottom: 30px; }
	@include media-breakpoint-down(sm) {
		margin-bottom: 20px; }
	.banner {
		.slick-prev, .slick-next {
			@include fs(0);
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			padding: 5px 10px;
			background: $green;
			z-index: 10;
			@include media-breakpoint-down(xs) {
				top: 65px;
				@include transform(translateY(0));
				padding: 3px 5px; }

			&:before {
				content: '\f104';
				font-family: 'FontAwesome';
				@include fs(24rem);
				color: $white;
				@include transition(all 0.3s); }
			&:hover {
				background: $green;
				&:before {
					color: $white; } } }
		.slick-prev {
			left: 0;
			@include transform(translate(-100%,-50%));
			@include bor-rad(0 3px 3px 0); }
		.slick-next {
			@include transform(translate(100%,-50%));
			right: 0;
			@include bor-rad(3px 0 0  3px);
			&:before {
				content: '\f105'; } }
		&:hover {
			.slick-prev, .slick-next {
				@include transform(translate(0,-50%)); } }
		.slick-dots {
			@include pos(abs, null null 30px 50%);
			@include transform(translateX(-50%));
			@include media-breakpoint-down(sm) {
				bottom: 15px;
				display: none !important; }

			li {
				@include fs(0);
				display: inline-block;
				+ li {
					margin-left: 10px; }
				button {
					width: 15px;
					height: 15px;
					background: darken($white, 30%);
					@include bor-rad(50%);
					@include transition(all 1s); }
				&.slick-active {
					button {
						background: $green; } } } }

		.bannerimg {
			@include flexbox;
			align-items: center;
			justify-content: center;
			// height: 570px
			// +media-breakpoint-down(lg)
			// 	height: 450px
			// +media-breakpoint-down(md)
			// 	height: 350px
			// +media-breakpoint-down(sm)
			// 	height: 280px
			// +media-breakpoint-down(xs)
			// 	height: 200px
			img {
				// max-width: unset
 } }				// height: 100%

		.bannercaption {
			@include pos(abs,null null 80px null);
			width: 35%;
			@include transform(translateY(100%));
			opacity: 0;
			color: #666;
			z-index: 999;
			@include transition(all, 1s);

			// background: rgba(0,0,0,0.3)
			// padding: 15px 20px
			@include media-breakpoint-down(lg) {
				bottom: 50px; }
			@include media-breakpoint-down(md) {
				width: 50%;
				bottom: 30px; }
			@include media-breakpoint-down(sm) {
				width: 50%; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				background: rgba(255,255,255,0.8);
				// bottom: 0
				padding: 20px 20px;
				text-align: center;
				// left: 0
				// right: 0
				position: static; }
			h3 {
				@include fs(24rem);
				color: $green;
				text-transform: uppercase;
				font-weight: 700;
				line-height: 1.25;
				margin-bottom: 10px;
				@include media-breakpoint-down(md) {
					@include fs(20rem); }
				@include media-breakpoint-down(sm) {
					line-height: 1.1;
					@include fs(18rem); }
				@include media-breakpoint-down(xs) {
					text-align: center; } }
			p {
				@include fs(16rem);
				line-height: 1.3;
				@include media-breakpoint-down(sm) {
					line-height: 1.1; } }


			.title {
				@include fs(40rem);
				text-transform: uppercase;
				font-weight: 800;
				color: $white;
				line-height: 1.3;
				text-shadow: 3px 3px 7px color-rgba($black,0.5);
				margin-bottom: 20px;
				@include media-breakpoint-down(md) {
					@include fs(24rem); }
				@include media-breakpoint-down(sm) {
					@include fs(22rem); }
				@include media-breakpoint-down(xs) {
					@include fs(18rem);
					margin-bottom: 0; } }
			.link {
				display: inline-block;
				background: $red;
				text-transform: uppercase;
				color: $white;
				font-weight: 400;
				line-height: 40px;
				padding: 0px 0px 0px 30px;
				overflow: hidden;
				@include bor-rad(10px);
				@include media-breakpoint-down(md) {
					line-height: 30px;
					padding-left: 20px; }
				@include media-breakpoint-down(xs) {
					display: none; }
				&:hover {
					background: darken($red,10%); }
				&:after {
					content: '+';
					display: inline-block;
					vertical-align: top;
					margin-left: 25px;
					@include fs(24rem);
					text-align: center;
					line-height: 34px;
					width: 40px;
					height: 40px;
					background: darken($red,10%);
					@include media-breakpoint-down(md) {
						margin-left: 15px; } } } }
		.item {
			&.slick-current {
				.bannercaption {
					@include transform(translateY(0%));
					opacity: 1;
					transition-delay: 1s !important; } } } }
	.container {
		@include pos(rel,null);
		.prev, .next {
			display: block;
			color: $white;
			background: color-rgba($black,0.5);
			text-align: center;
			line-height: 50px;
			width: 50px;
			cursor: pointer;
			@include transition(all,0.5s);
			@include media-breakpoint-down(sm) {
				display: none;
				width: 35px;
				height: 35px;
				line-height: 35px; }
			&:hover {
				background: color-rgba($black,0.7); } }
		.prev {
			@include pos(abs, -50px 67px null null);
			@include media-breakpoint-down(sm) {
				top: -35px;
				right: 53px; } }
		.next {
			@include pos(abs,-50px 15px null null);
			@include media-breakpoint-down(sm) {
				top: -35px; } } } }
