.contact-wrap {
	.contact-content {
		padding: 50px 30px;
		background: whitesmoke;
		border-radius: 4px;
		@include media-breakpoint-down(md) {
			margin-bottom: 20px;
			padding: 20px; }
		@include media-breakpoint-down(xs) {
			padding: 15px 10px; }
		h3 {
			color: #01a54f;
			@include fs(18rem);
			font-weight: 600;
			margin-bottom: 10px;
			display: inline;
			margin-right: 10px; }
		a {
			color: $main; }

		.map {
			margin-top: 10px;
			.fa {
				display: inline-block;
				width: 20px;
				text-align: center;
				color: #01a54f;
				margin-right: 7px; } }
		.nav.nav-tabs {
			border: none;
			.nav-item {
				margin-bottom: 10px;
				padding-bottom: 10px;
				border-bottom: 1px dashed darken($white, 15%);
				@include media-breakpoint-down(xs) {
					padding-bottom: 5px;
					margin-bottom: 5px; }
				&:last-child {
					border: none;
					margin-bottom: 0;
					padding-bottom: 0; }
				.nav-link {
					background: none;
					border: none; } } } }
	.contact-map {
		height: 414px;
		@include media-breakpoint-down(xs) {
			height: 300px; }
		iframe {
			height: 100%; } }
	.contact-form {
		max-width: 500px;
		margin: auto; }
	.contact-form-wrap {
		margin: 15px 0;
		.form-group {
			margin-bottom: 5px;
			input, textarea {
				width: 100%;
				border: 1px solid #d9d9d9;
				border-radius: 3px;
				color: $main;
				padding: 0 15px;
				height: 40px;
				line-height: 40px;
				border-radius: 5px;
				font-style: italic; }
			textarea {
				height: 120px; } }
		.frm-btn {
			margin-top: 10px;
			input[type="submit"] {
				border: none;
				@include mainbutton; } } } }
