section.breadcrumb-wrap {
	padding: 0;
	position: relative;
	width: 100%;
	// margin-bottom: 40px
	background-color: none;
	overflow: hidden;
	// height: 350px
	// +media-breakpoint-down(lg)
	// 	height: 300px
	// +media-breakpoint-down(md)
	// 	height: 250px
	// +media-breakpoint-down(sm)
	// 	height: 200px
	// +media-breakpoint-down(xs)
	// 	height: 130px
	/// margin-bottom: 30px
	// &:before
	// 	content: ''
	// 	+pos(abs, 0 0 0 0)
	// 	background: rgba(0,0,0,0.3)
	.breadcrumb-slide {
		.item {}
		.breadcrumb-bg {
			width: 100%;
			height: 350px;
			@include media-breakpoint-down(lg) {
				height: 300px; }
			@include media-breakpoint-down(md) {
				height: 250px; }
			@include media-breakpoint-down(sm) {
				height: 200px; }
			@include media-breakpoint-down(xs) {
				height: 130px; } }
		.slick-prev, .slick-next {
			@include fs(0);
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			padding: 5px 10px;
			background: $green;
			z-index: 10;

			&:before {
				content: '\f104';
				font-family: 'FontAwesome';
				@include fs(24rem);
				color: $white;
				@include transition(all 0.3s); }
			&:hover {
				background: lighten($green, 10%);
				&:before {
					color: $white; } } }
		.slick-prev {
			left: 0;
			@include transform(translate(-100%,-50%)); }
		.slick-next {
			@include transform(translate(100%,-50%));
			right: 0;
			&:before {
				content: '\f105'; } }
		&:hover {
			.slick-prev, .slick-next {
				@include transform(translate(0,-50%)); } } }

	.tp-banner {
		ul {
			li {

				img {
					max-width: unset;
					height: 100%; } } } }

	.pagename {
		@include pos(abs, 50% null null 50%);
		@include transform(translate(-50%, -50%));
		z-index: 10;
		color: $white;
		@include fs(36rem);
		text-transform: uppercase;
		font-weight: 300;

		&:after {
			content: '';
			display: block;
			margin: auto;
			height: 10px;
			width: 130px;
			background: url(../img/title-white.png) top center no-repeat;
			margin-top: 5px;
			z-index: 100; } }

	.breadcrumb-content {
		width: 100%;
		@include pos(abs, null null 0 0);
		z-index: 50;
		background: rgba(255,255,255,0.7);
		// &:before
		// 	content: ''
		// 	+pos(abs, 0 0 0 0)
		// 	background: -webkit-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.2), rgba(0,0,0,0.4))
		// 	background: -o-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.2), rgba(0,0,0,0.4))
		// 	background: -moz-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.2), rgba(0,0,0,0.4))
		// 	background: linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.2), rgba(0,0,0,0.4))
		// 	z-index: -1

		ol {
			float: left;
			margin-bottom: 0;
			list-style: none;
			background: none;
			padding: 12px 0;
			@include media-breakpoint-down(xs) {
				padding: 8px 0; }


			li {
				float: left;
				margin-right: 8px;
				@include media-breakpoint-down(xs) {
					display: none; }
				&:before {
					content: '\f138';
					font-family: 'FontAwesome';
					float: right;
					margin-left: 12px;
					color: darken($white, 60%); }
				&:last-child {
					@include media-breakpoint-down(xs) {
						display: block; }
					&:before {
						display: none; } }
				a {
					display: inline-block;
					color: darken($white, 60%);
					@include fs(14rem);
					&:hover {
						color: $green; } } } } } }
