.tuyendung {
	.pagetitle {} }

.career-content {
	table.table {
		margin-bottom: 30px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 20px; }
		thead {
			tr {
				th {
					border: none;
					border-bottom: 2px solid #ccc;
					white-space: nowrap; } } }
		tbody {
			tr {
				td {
					cursor: pointer;
					border-top: none;
					border-bottom: 1px solid #e1e1e1;
					vertical-align: middle;

					&:nth-child(1) {
						// padding-left: 40px
						position: relative;
						// +transition(all 0.2s)
						font-weight: 500;
						min-width: 350px;
						// &:before
						// 	content: '\f105'
						// 	font-family: 'FontAwesome'
						// 	+fs(20rem)
						// 	+pos(abs, 50% null null 15px)
						// 	+transform(translateY(-50%))
						// 	float: left
						// 	font-weight: 500
 }						// 	+transition(all 0.2s)
					&:nth-child(2) {
						width: 130px;
						font-weight: 300;
						span {
							width: 64px;
							background: $green;
							color: $white;
							display: inline-block;
							text-align: center;
							@include fs(13rem);
							@include bor-rad(20px);
							padding: 3px 0; } }
					&:nth-child(3) {
						min-width: 140px;
						font-weight: 300; }
					&:nth-child(4), &:nth-child(5) {
						font-weight: 300;
						min-width: 110px; } }
				&.tr-even {
					td {
						background: darken($white, 4%) !important; } }

				&:hover {
					background: darken($white, 10%) !important;
					td {
						background: darken($white, 10%) !important; } }
				&.active {
					td {
						&:nth-child(1) {
							color: $green; } } } } } }


	.chinhsachtuyendung {
		max-width: 950px;
		color: darken($white, 60%);
		margin: auto;
		text-align: center;
		@include fs(18rem);
		@include media-breakpoint-down(sm) {
			@include fs(16rem); }
		hr {
			width: 50px;
			height: 1px;
			background: $green;
			margin: 10px auto; } }
	.cachthucungtuyen {
		padding-top: 20px;
		@include media-breakpoint-down(md) {
			padding: 0; }
		.title {
			position: relative;
			padding-left: 70px;
			@include media-breakpoint-down(xs) {
				padding-left: 0px; }
			h3 {
				@include fs(18rem);
				line-height: 1.3;
				vertical-align: middle;
				margin-bottom: 20px;
				@include media-breakpoint-down(xs) {
					padding-top: 0 !important;
					min-height: unset; }
				a {
					@include fs(15rem); } } }

		.icon {
			max-height: 46px;
			@include pos(abs, 50% null null 0);
			@include transform(translateY(-50%));
			@include media-breakpoint-down(xs) {
				position: static;
				display: none; } }

		> .row {
			.col-xs-12 {
				margin-bottom: 30px;
				@include media-breakpoint-down(xs) {
					margin-bottom: 20px; } } }
		ul {
			margin-bottom: 30px;
			border-bottom: 1px dashed darken($white, 15%);
			padding-bottom: 20px;
			@include media-breakpoint-down(xs) {
				margin-bottom: 20px; }
			li {
				margin-bottom: 10px;
				&:before {
					background: $main; } } } } }



.career-detail {
	padding: 40px 0 30px;
	@include media-breakpoint-down(sm) {
		padding: 30px 0 15px; }
	time {
		display: block;
		@include fs(13rem);
		color: darken($white, 40%);
		margin-bottom: 10px;

		&:before {
			content: '\f017';
			font-family: 'FontAwesome';
			// +fs(16rem)
			display: inline-block;
			margin-right: 5px; } }
	.pagetitle {
		margin-bottom: 10px; }

	.content {
		h5 {
			color: $main;
			font-weight: 500;
			@include fs(18rem); }
		hr {
			border-top: 1px dashed darken($white, 12%); } }
	.button-group {
		.frame-link {
			@include mainbutton; } } }


.other-career {
	margin-bottom: 30px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 20px; }
	.title-post {
		color: $main;
		font-weight: 700;
		@include fs(22rem);
		position: relative;
		margin-bottom: 15px;
		@include media-breakpoint-down(sm) {
			@include fs(20rem); }
		&:after {
			content: '';
			display: block;
			width: 50px;
			height: 2px;
			background: $green;
			margin-top: 5px; }
		&.orange {
			&:after {
				background: $green; } } }

	.postlist {
		.post {
			padding: 10px 0;
			border-bottom: 1px dashed darken($white,10%);
			@include flexbox;
			flex-wrap: wrap;
			time {
				width: 110px;
				height: 25px;
				line-height: 25px;
				@include fs(12rem);
				background: darken($white,10%);
				color: $main;
				font-weight: 500;
				padding: 0px 8px;
				@include bor-rad(3px);
				margin-bottom: 5px;
				text-align: center; }
			.postname {
				width: calc(100% - 110px);
				line-height: 1.5;
				padding-left: 15px;
				vertical-align: middle;
				a {
					color: $main;
					@include fs(16rem);
					&:hover {
						color: $green; } } } } } }

.career-wrap {
	.headtitle {
		margin-bottom: 15px; }
	.content {
		h3 {
			font-weight: 500;
			@include fs(18rem); } } }

.career-nav {
	margin-bottom: 30px;
	border-bottom: 1px solid darken($white, 10%);
	@include media-breakpoint-down(sm) {
		margin-bottom: 20px; }
	@include media-breakpoint-down(xs) {
		padding: 5px 0; }
	ul {
		text-align: center;

		li {
			display: inline-block;
			+ li {
				margin-left: 20px;
				@include media-breakpoint-down(lg) {
					margin-left: 10px; }
				@include media-breakpoint-down(sm) {
					margin: 0; } }
			a {
				color: darken($white, 40%);
				@include fs(16rem);
				text-transform: uppercase;
				display: block;
				padding: 0 20px;
				line-height: 60px;
				@include bor-rad(5px);
				font-weight: 700;
				@include media-breakpoint-down(lg) {
					@include fs(15rem);
					line-height: 50px; }
				@include media-breakpoint-down(sm) {
					@include fs(15rem);
					line-height: 40px; }
				@include media-breakpoint-down(xs) {
					line-height: 35px; }
				&:after {
					content: '';
					display: block;
					@include transition(all 0.3s);
					height: 3px;
					width: 0;
					background: $green;
					margin: auto;
					margin-bottom: -2px;
					@include media-breakpoint-down(xs) {
						display: none; } }
				&:hover {
					&:after {
						width: 100%; } } }
			&.active {
				a {
					color: $green;
					&:after {
						background: $green;
						width: 100%; } } } } } }
.career-filter {
	margin-bottom: 20px;
	text-align: center;
	select {
		@include bor-rad(5px);
		border-color: darken($white, 15%);
		padding: 8px 15px;
		min-width: 200px;
		color: darken($white, 60%);
		background: darken($white, 5%);
		@include media-breakpoint-down(sm) {
			margin-bottom: 5px; }
		@include media-breakpoint-down(xs) {
			width: 100%; } } }
.career-wrap {
	.content {
		color: darken($white, 60%);
		h3 {
			color: $main;
			font-weight: 900;
			@include fs(24rem);
			margin-bottom: 20px; }
		p {}

		figure {
			text-align: center;
			margin-top: 20px;
			.img {
				@include flexbox;
				align-items: center;
				justify-content: center;
				margin-bottom: 10px;
				img {
					max-width: 100%;
					max-height: 128px; } }
			figcaption {
				text-align: center;
				h2 {
					@include fs(20rem);
					color: $main;
					line-height: 1.2;
					margin-bottom: 10px;
					min-height: 59px;
					font-weight: 500;
					&:after {
						content: '';
						display: block;
						width: 50px;
						height: 1px;
						background: #000;
						margin: auto;
						margin-top: 10px; } }
				p {
					color: darken($white, 60%); } } }
		.btn-register {
			@include mainbutton;
			margin-top: 10px; } } }
