.home-top {
	background-size: cover !important;
	padding-bottom: 50px;
	@include media-breakpoint-down(md) {
		background-position: left top !important; }
	@include media-breakpoint-down(sm) {
		padding: 30px 0; } }
.home-title {
	text-align: center;
	@include fs(24rem);
	text-transform: uppercase;
	font-weight: 900;
	color: $green;
	margin-bottom: 35px;
	@include media-breakpoint-down(sm) {
		@include fs(20rem);
		margin-bottom: 20px; }
	@include media-breakpoint-down(xs) {
		@include fs(18rem); } }

