.home-career {
	padding: 50px 0;
	background-size: cover !important;
	@include media-breakpoint-down(md) {
		background-position: left top !important; }
	@include media-breakpoint-down(sm) {
		padding: 20px 0; }
	.home-step-wrap {
		@include flexbox;
		position: relative;
		flex-wrap: wrap;
		justify-content: center;
		.step-item {
			width: 20%;
			padding: 0 16px;
			@include media-breakpoint-down(lg) {
				padding: 0 8px; }
			@include media-breakpoint-down(md) {
				width: 50%;
				margin-bottom: 18px; }
			@include media-breakpoint-down(xs) {
				width: 100%; } }

		figure {
			box-shadow: 0 2px 5px rgba(0,0,0,0.05);
			text-align: center;
			height: 100%;
			background: $white;
			@include bor-rad(10px);
			padding: 20px 0;
			@include media-breakpoint-down(sm) {
				width: 100%;
				margin-bottom: 30px;
				padding-bottom: 0; }

			.item-icon {
				margin-bottom: 20px;
				position: relative;
				z-index: 10;
				img {
					max-height: 54px;
					max-width: unset;
					object-fit: contain; } }


			figcaption {
				color: darken($white, 60%);
				padding: 0 20px;
				max-width: 280px;
				display: inline-block;
				@include media-breakpoint-down(lg) {
					padding: 0 10px; }
				@include media-breakpoint-down(md) {
					padding: 0 10px; }


				h3 {
					@include fs(16rem);
					text-transform: uppercase;
					color: darken($white, 60%);
					margin-bottom: 10px;
					font-weight: 800;
					line-height: 1.25;
					min-height: 38px;
					@include media-breakpoint-down(sm); } } } }

	.home-process {
		text-align: center;
		margin: 40px 0;
		@include media-breakpoint-down(sm) {
			margin: 20px 0; }
		figure {
			display: inline-block;
			margin: 20px 80px;
			@include media-breakpoint-down(sm) {
				margin: 10px 20px; }
			a {
				@include flexbox;
				align-items: center;
				justify-content: center;
				flex-wrap: wrap;
				align-content: center;
				height: 170px;
				width: 170px;
				@include bor-rad(50%);
				color: $white;
				text-align: center;
				margin: auto;
				position: relative;
				overflow: hidden;
				@include fs(20rem);
				font-weight: 700;

				img {
					@include transition(all 0.5s); }
				&:after {
					content: '';
					background: $white;
					@include pos(absolute, -120px null null -200px );
					opacity: 0.4;
					width: 80px;
					height: 400px;
					@include rotate(35deg);
					z-index: 10; }



				p, span {
					width: 100%; }

				span {
					@include fs(20rem);
					margin-top: 5px; } }

			&:nth-child(1) {
				a {
					background: #08a870;
					// &:after
					// 	-webkit-animation: change-bg-1 2.5s infinite
					// 	animation: change-bg-1 2.5s infinite
					// @-webkit-keyframes "change-bg-1"
					// 	0%
					// 		left: -200px
					// 	30%
					// 		left: 150%
					// 	100%
					// 		left: 200%
					@-webkit-keyframes "change-bg-2" {
						0% {
							left: -200px; }
						25% {
							left: -200px; }
						55% {
							left: 150%; }
						100% {
							left: 200%; } } } }
			&:nth-child(2) {
				a {
					background: #01a54f;
					-webkit-animation: change-color 2s infinite;
					animation: change-color 2s infinite;
					&:after {
						-webkit-animation: change-bg-2 2.5s infinite;
						animation: change-bg-2 2.5s infinite; }
					@-webkit-keyframes "change-color" {
						0% {
							color: $white; }
						50% {
							color: $yellow; }
						100% {
							color: $white; } } } } } } }

.home-video-product {
	padding: 50px 0 20px;
	@include media-breakpoint-down(sm) {
		padding: 30px 0 0; }
	.home-video {
		margin-bottom: 20px;
		figure {
			.item-img {
				position: relative;
				overflow: hidden;
				display: block;
				&:before {
					content: '';
					@include pos(abs, 0 0 0 0);
					width: 110px;
					height: 110px;
					background: url('../img/home/icon-video.png') center center no-repeat;
					margin: auto;
					background-size: cover;
					z-index: 10;
					@include media-breakpoint-down(sm) {
						height: 60px;
						width: 60px; } }
				&:after {
					content: '';
					@include pos(abs, 0 0 0 0);
					background: rgba(0,0,0,0.3); }


				img {
					@include transition(all 0.5s); }
				&:hover {
					img {
						@include transition(all 5s);
						@include transform(scale(1.3,1.3)); } } } } }
	.home-product {

		figure {
			@include flexbox;
			flex-wrap: wrap;
			.item-img {
				width: 50%;
				overflow: hidden;
				@include media-breakpoint-down(sm) {
					width: 100%; }
				img {
					@include transition(all 0.3s); }
				&:hover {
					img {
						@include transform(scale(1.1,1.1)); } } }
			figcaption {
				width: 50%;
				position: relative;
				background: darken($white, 5%);
				color: darken($white, 60%);
				text-align: center;
				padding: 0 70px;
				@include flexbox;
				flex-wrap: wrap;
				align-content: center;
				justify-content: center;
				@include media-breakpoint-down(md) {
					padding: 0 20px; }
				@include media-breakpoint-down(sm) {
					padding: 30px;
					width: 100%; }
				@include media-breakpoint-down(xs) {
					padding: 20px; }


				h3 {
					@include fs(20rem);
					text-transform: uppercase;
					color: $green;
					margin-bottom: 10px;
					font-weight: 700;
					@include media-breakpoint-down(xs) {
						@include fs(18rem); } }

				p {} } } } }
