// ECOMERCE
.input-number {
	@include pos(rel,null);
	width: 90px;
	.btn-spin {
		@include pos(abs,0 null null null);
		width: 30px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		cursor: pointer;
		&.btn-dec {
			left: 0; }
		&.btn-inc {
			right: 0; } }
	input {
		width: 100%;
		border: 1px solid darken($white,10%);
		@include bor-rad(5px);
		@include fs(17rem);
		font-weight: 700;
		color: $black;
		text-align: center;
		padding: 4px 10px; } }

