.login-page {
	// background: darken($white,3%)
	.login-panel {
		margin: 60px 0;
		background: $white;
		// box-shadow: 0 0 50px color-rgba($black,0.1)
		@include bor-rad(5px);
		@include media-breakpoint-down(lg) {
			margin: 60px 0; } }
	.sitelogo {
		text-align: center;
		margin-bottom: 40px;
		width: 100%;
		img {
			height: 80px; }
		@include media-breakpoint-down(xs) {
			margin-bottom: 30px; } }
	.headtitle {
		text-align: center;
		@include fs(18rem);
		margin-bottom: 30px;
		&:after {
			content: '';
			margin: 10px auto; } } }
.login-form {
	width: 100%;
	margin: 0 auto;
	.title {
		@include fs(24rem);
		font-weight: 500;
		text-transform: uppercase;
		border-left: 3px solid $red;
		padding-left: 10px;
		margin-bottom: 30px;
		// &:after
		// 	content: ''
		// 	display: block
		// 	width: 50px
		// 	height: 2px
		// 	background: $red
 }		// 	margin: 10px auto
	.module-title {
		@include fs(14rem);
		// font-weight: 300
		color: darken($white,60%);
		margin-bottom: 20px;
		a {
			color: $red;
			&:hover {
				lighten($red,10%) {} } } }
	.form-group {
		margin-bottom: 20px;
		@include pos(rel,null);
		label {
			color: darken($white,40%);
			text-transform: uppercase;
			@include fs(18rem);
			display: block;
			width: 50px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			@include pos(abs, 6px null null 0);
			border-right: 1px solid darken($white,10%);
 }			// display: none
		input {
			width: 100%;
			@include fs(18rem);
			border: 1px solid darken($white,15%);
			@include bor-rad(5px);
			padding: 15px 30px 15px 60px;
			@include transition(all,0.5s);
			&:active, &:hover, &:focus {
				border-color: darken($white,40%); } } }
	.forget {
		@include fs(14rem);
		color: darken($white,40%);
		margin-bottom: 30px;
		a {
			color: $red;
			&:hover {
				color: $black; } } }
	.btn-login {
		margin: 0 auto;
		width: 100%;
		text-align: center;
		@include pos(rel,null);
		color: $white;
		background: $red;
		display: block;
		@include bor-rad(5px);
		overflow: hidden;
		@include media-breakpoint-down(xs) {
			width: 100%; }
		&:before {
			background: darken($red,10%); }
		&:after {
			content: '\f105';
			font-family: 'fontawesome';
			@include pos(abs, 12px 15px null null); } }
	input[type='reset'] {
		display: none; }
	input[type="submit"] {
		width: 100%;
		color: $white;
		background: none;
		padding: 15px 60px;
		border: none;
		text-transform: uppercase;
		@include fs(16rem); }
	.go-signup {
		text-align: center;
		margin-top: 15px;
		a {
			display: block;
			clear: both;
			color: $red;
			&:hover {
				color: $black; } } } }
.login-bg {
	width: 60%;
	@include pos(rel,null);
	overflow: hidden;
	padding: 50px;
	@include bor-rad(5px);
	@include media-breakpoint-down(md) {
		width: 100%;
		order: 1; }
	@include media-breakpoint-down(sm) {
		padding: 30px; }
	.login-img {
		@include pos(abs, 0 null 0 0);
		right: 0;
		.login-overlay {
			display: block;
			@include pos(abs, 0 null 0 0);
			right: 0;
			background: $black;
			opacity: 0.5;
			z-index: 1; }
		img {
			height: 100%;
			width: auto;
			max-width: unset;
			filter: grayscale(100%);
			@include media-breakpoint-down(md) {
				height: auto;
				width: 100%; }
			@include media-breakpoint-down(sm) {
				@include transform(scale3d(1.1,1.1,1)); }
			@include media-breakpoint-down(xs) {
				width: auto;
				height: 100%; } } }
	.login-des {
		@include pos(rel,null);
		z-index: 1;
		color: $white;
		.title {
			@include fs(46rem);
			font-weight: 300;
			margin-bottom: 20px;
			// opacity: 0.8
			@include media-breakpoint-down(sm) {
				@include fs(36rem);
				margin-bottom: 10px; } }
		.module-title {
			@include fs(26rem);
			font-weight: 200;
			opacity: 0.9;
			@include media-breakpoint-down(sm) {
				@include fs(20rem); } }
		.des {
			margin-top: 180px;
			@include fs(20rem);
			@include media-breakpoint-down(md) {
				margin-top: 30px; }
			@include media-breakpoint-down(sm) {
				margin-top: 10px; }
			span {
				opacity: 0.8;
				font-weight: 200;
				display: inline-block;
				margin-right: 10px; }
			a {
				display: inline-block;
				opacity: 1;
				font-weight: 300;
				@include fs(32rem);
				color: $white;
				@include media-breakpoint-down(xs) {
					@include fs(26rem); }
				&:after {
					content: '\f105';
					font-family: 'fontawesome';
					margin-left: 5px; }
				&:hover {
					color: $red; } } } } }

