			@include fs(13rem);
			margin-bottom: 5px; }
		input, select {
			width: 100%;
			@include fs(15rem);
			border: 1px solid darken($white,15%);
			@include bor-rad(5px);
			padding: 10px 15px;
			@include transition(all,0.5s);
			&:active, &:hover, &:focus {
				border-color: darken($white,40%); } }
		select {
			padding: 9px 15px; } }
	.gender-select {
		ul {
			padding: 10px 0 0;
			&:after {
				display: table;
				content: '';
				width: 100%;
				clear: both; }
			li {
				width: 150px;
				float: left; } }
		input[type="radio"] {
			display: none;
			+ label {
				font-weight: 500;
				color: $main;
				padding-left: 30px;
				@include pos(rel,null);
				@include transition(all,0.5s);
				&:hover {
					color: $black; }
				&:before {
					content: '';
					display: block;
					width: 16px;
					height: 16px;
					border: 1px solid darken($white,40%);
					opacity: 0.5;
					@include bor-rad(50%);
					@include pos(abs, 1px null null 0);
					@include transition(all,0.5s); }
				&:after {
					content: '';
					display: block;
					width: 14px;
					height: 14px;
					border: 3px solid $white;
					background: $red;
					opacity: 0;
					@include bor-rad(50%);
					@include pos(abs, 2px null null 1px);
					@include transition(all,0.5s); } }
			&:checked {
				+ label {
					&:before, &:after {
						opacity: 1; } } } } }
	.birthday {
		ul {
			margin: 0 -10px;
			li {
				width: 33.33%;
				float: left;
				padding: 0 10px; } } }

	.confirm {
		padding-top: 10px;
		.check-group {
			input[type='checkbox'] {
				display: none;
				+ label {
					color: darken($white,60%);
					font-weight: 300;
					padding-left: 30px;
					@include fs(14rem);
					@include pos(rel,null);
					@include transition(all,0.5s);
					&:hover {
						color: $black; }
					&:before {
						content: '';
						display: block;
						width: 16px;
						height: 16px;
						border: 1px solid darken($white,40%);
						opacity: 0.5;
						@include pos(abs, 1px null null 0);
						@include transition(all,0.5s); }
					&:after {
						content: '\f00c';
						font-family: 'fontawesome';
						display: block;
						line-height: 14px;
						@include fs(14rem);
						color: $red;
						opacity: 0;
						@include pos(abs, 2px null null 1px);
						@include transition(all,0.5s); } }
