// USER SIDEBAR

.user-sidebar {
	margin: 60px 0 20px;
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
		&:after {
			content: '\f107';
			font-family: 'fontawesome';
			@include pos(abs, 50% 15px null null);
			@include transform(translateY(-50%));
			color: $white;
			@include fs(24rem); } }
	.userinfo {
		background: $lblack;
		// border-left: 5px solid $red
		figure {
			padding: 10px;
			&:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			.user-img {
				display: block;
				width: 80px;
				float: left;
				@include media-breakpoint-down(lg) {
					width: 60px; } }
			figcaption {
				display: table-cell;
				padding: 15px;
				@include media-breakpoint-down(lg) {
					padding: 7px 15px; }
				.title {
					color: $white;
					opacity: 0.8;
					font-weight: 300;
					@include media-breakpoint-down(lg) {
						@include fs(13rem); } }
				.user-name {
					text-transform: uppercase;
					font-weight: 500;
					@include fs(18rem);
					color: $white;
					@include media-breakpoint-down(lg) {
						@include fs(15rem); } } } } } }
.user-sidelink {
	background: darken($white,5%);
	margin-bottom: 60px;
