			border: 1px solid darken($white, 15%);
			font-size: 0.9375rem;
			color: $black;
			@include border-radius(0); }
		input[type="submit"] {
			border: none;
			height: 35px;
			line-height: 35px;
			text-align: center;
			padding: 0 20px;
			color: $white;
			background-color: $red; } }
	.searchresultlist {
		margin-top: 20px; }
	.searchresult {
		border-bottom: 1px solid darken($white, 15%);
		margin: 0 0 15px;
		padding: 0 0 15px;
		h3 {
			font-size: 16px;
			font-weight: 600;
			margin: 0 0 10px;
			color: $black;
			a {
				color: $blue;
				text-decoration: none;
				&:hover {
					color: $red; } } } }
	hr {
		display: none; } }


// SEARCH SUGGEST
.suggestsearch {
	position: absolute!important;
	top: 100%;
	left: 0;
	width: 100%;
	z-index: 401;
	ul {
		margin: 0;
		padding: 0;
		li {
			list-style: none;
			display: block;
			border-bottom: 1px dotted darken($white, 10%);
			&:before, &:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			&:hover,
			&.selected {
				background: darken($white, 10%); }

			&.prdlist_icon {
				margin-bottom: 0;
				height: auto; }
			.product-img {
				width: 100px;
				height: 100px;
				overflow: hidden;
				display: flex;
				justify-content: center;
				align-content: center;
				float: left;
				background: $white;
				border: 1px solid darken($white,10%);
				img {
					max-height: 90%;
					max-width: 90%; } }
			.prd-info {
				width: calc(100% - 100px);
				padding: 10px;
				float: left; }
			a {
				padding: 10px;
				display: block;
				.prd-info {
					text-align: left;
					h3,
					h4 {
						font-size: 16px;
						font-weight: 400;
						margin: 0 0 5px;
						line-height: 1.4em; }

					h3 {
						font-size: 14px;
						color: darken($red,10%);
						font-weight: 700; }

					h4 {
						color: darken($red,10%); }

					span {
						display: block;
						&.price {
							color: $black; }

						&.price_original {
							color: $black;
							text-decoration: line-through; } } } } } } }
